    
    
    <template>
  <div class="homeIndex">
    <div class="header">
      <div class="center">
        <div class="logoBox">
          <img class="logo" style="margin-top:15px;" :src="logoSrc" alt />
        </div>
        <div class="nav">
          <a href="/home">首页</a>
          <a>服务优势</a>
          <a>客户案例</a>
          <a>帮助文档</a>
          <!-- <a href="#contact">联系</a> -->
        </div>
        <div class="loginInfo">
          <div class="wrapper">
            <el-button @click="loginBtn" type="primary">{{
              loginState
            }}</el-button>
          </div>
        </div>
      </div>
    </div>
    <div class="banner">
      <el-carousel height="317px">
        <el-carousel-item v-for="(img, i) in bannerArr" :key="i">
          <img class="bannerImg" :src="img" alt="" />
          <!-- <h3 class="small">{{ item }}</h3> -->
        </el-carousel-item>
      </el-carousel>
    </div>

    <router-view />
    <footerN></footerN>

    <!-- 登陆框 -->
    <el-dialog
      title="登陆/注册"
      :visible.sync="dialogVisible"
      :close-on-click-modal = "false"
      width="420px"
      class="loginBox"
      :before-close="close"
    >
      <div>
        <el-form
          :model="login"
          :rules="loginRules"
          ref="login"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="手机号码:" prop="phoneNum">
            <el-input v-model.number="login.phoneNum"></el-input>
          </el-form-item>
          <div class="loginCode">
            <el-form-item label="验证码:" prop="code">
              <el-input v-model.number="login.code"></el-input>
            </el-form-item>
            <el-button type="primary" style="font-size:12px;"   @click="getCode">获取验证码</el-button>
          </div>
          <div class="f" style="font-size:12px;">
            <el-checkbox  v-model="is_argee" :label="is_argee"
              >我同意《用户使用协议》</el-checkbox
            >
            <p>注：未注册用户，首次登陆自动注册成为会员</p>
          </div>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="close">取 消</el-button>
        <el-button
          type="primary"
          @click="submit('login')"
          v-loading.lock="fullscreenLoading"
          >登陆</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>
<script>
window.onload = function () {};
import banner1 from "../../assets/banner1.jpg";
import banner2 from "../../assets/banner2.jpg";
import banner3 from "../../assets/banner3.jpg";
import footerN from "../../components/footer";

export default {
  components: {
    footerN,
  },
  data() {
    return {
      hdgd: "320px",
      is_argee: false,
      fullscreenLoading: false,
      bannerArr: [banner1, banner2, banner3],
      loginState: "登陆",
      logoSrc: require("../../assets/new_logo.jpg"),
      login: {
        phoneNum: "",
        code: "",
      },
      loginRules: {
        code: [
          { required: true, message: "请获取验证码", trigger: "blur" },
          { type: "number", message: "验证码应为数字" },
        ],
      },
      banner: [""],

      dialogVisible: false,
    };
  },
  mounted() {
    this.loginState = JSON.parse(localStorage.getItem("userInfo"))
      ? "控制台"
      : "登陆";
    let nav = document.getElementsByClassName("nav")[0].children;
    for (let i = 0; i < nav.length; i++) {
      (function (j) {
        nav[j].onclick = function () {
          let navText = nav[j].innerHTML;
          switch (navText) {
            case "服务优势":
              window.location.href = window.location.origin + "/home#serve";
              break;
            case "帮助文档":
              window.location.href = window.location.origin + "/home/help";
              break;
            case "客户案例":
              window.location.href = window.location.origin + "/home#customer";
              
          }
        };
      })(i);
    }
  },
  methods: {
    loginBtn() {
      if (this.loginState == "登陆") {
        this.dialogVisible = true;
      } else {
        this.$router.push("/console");
      }
    },
    close() {
      this.dialogVisible = false;
      this.login = {
        phoneNum: "",
        code: "",
      };
      this.$refs["login"].resetFields();
    },
    getCode() {
      var phoneReg = /^[1]{1}[0-9]{10}$/;
      if (this.login.phoneNum == "") {
        this.$message.error("请填写手机号");
        return;
      }
      if (!phoneReg.test(this.login.phoneNum)) {
        this.$message.error("请填写正确的手机号");
        return;
      }
      this.axios
        .post("/v1/common/sms/sendVCode", { mobile: this.login.phoneNum })
        .then((res) => {
        });
    },
    submit(formName) {
      if (!this.is_argee) {
        this.$message.error("请选择同意协议");
        return;
      }
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.axios
            .post("/v1/passport/Login/index", {
              user_name: this.login.phoneNum,
              v_code: this.login.code,
            })
            .then((res) => {
              if (res.data.code == 200) {
                this.loginState = "控制台";
                this.dialogVisible = false;
                localStorage.setItem(
                  "userInfo",
                  JSON.stringify(res.data.result)
                );
                this.$router.push("/console");
                //获取渠道
                this.axios.get("/v1/config/Channel/list").then((res) => {
                  localStorage.setItem(
                    "channel",
                    JSON.stringify(res.data.result)
                  );
                });
                this.close();
              } else {
                this.$message.error(res.data.message);
              }
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
  },
};
</script>


<style lang="less">
// .homeIndex {
//   .el-carousel__container {
//     height: 320px !important;
//   }
// }
.loginBox{
  .el-form-item__content{
    width: 60%;
  }
}
.loginCode {

  overflow: hidden;
  .el-form-item {
    float: left;
    margin-bottom: 16px;
  }
  .el-button {
    float: left;
    height: 40px;
    padding: 0;
    width: 20%;
  }
}
.f {
  margin-left: 10%;
  p {
    margin-top: 6px;
  }
}
.center {
  width: 1200px;
  margin: 0 auto;
  padding-bottom: 50px;
}
@media(max-width: 1430px){
 .loginCode{
   .el-form-item{
    //  width: 220px;
    width: 30%;
   }
   .el-button{
     margin-left: 32%;
   }
   .el-form-item__content{
     width: 100%;
    //  float: left;
   }
 } 
}
</style>
<style lang="less" scoped>



.header {
  height: 80px;
  line-height: 80px;
  .center {
    .nav {
      margin-left: 6%;
      a{
        cursor: pointer;
      }
      & > * {
        margin: 0 42px;
        &:hover {
          color: #409eff;
        }
      }
    }
    & > * {
      float: left;
    }
    .loginInfo {
      float: right;
    }
  }
}
.center {
  width: 1200px;
  margin: 0 auto;
  padding-bottom: 50px;
}
.bannerImg {
  width: 100%;
  height: 320px;
}
</style>
