import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/home/index.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: "/home",
    component: Home
  }, {
    path: '/home',
    name: "home",
    component: Home,
    children: [
      {
        path: '/home',
        name: 'home',
        component: () => import(/* webpackChunkName: "about" */ '../views/home/home.vue'),
      },{
        path: 'help',
        name: 'help',
        component: () => import(/* webpackChunkName: "about" */ '../views/home/helpList.vue'),
      },{
        path: 'helpdetail',
        name: 'helpdetail',
        component: () => import(/* webpackChunkName: "about" */ '../views/home/helpDetail.vue'),
      },
    ]
  },
  {
    path: '/console',
    name: 'console',
    redirect: "/console/console",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/index.vue'),
    children: [
      {
        path: 'console',
        name: 'console',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/console.vue'),
      },
      {
        path: "domain",
        component: () => import('../views/domain.vue')
      },
      {
        path: "website",
        component: () => import('../views/website.vue')
      }, {
        path: "visitor",
        component: () => import('../views/visitor.vue')
      },
      {
        path: "page",
        component: () => import('../views/page.vue')
      },
      {
        path: "channel",
        component: () => import('../views/channel.vue')
      }, {
        path: "geographical",
        component: () => import('../views/geographical.vue')
      },
      {
        path: "order",
        component: () => import('../views/order.vue')
      },{
        path: "set",
        component: () => import('../views/set.vue')
      }
    ]
  }

]

const router = new VueRouter({
  routes,
  mode: 'history'
})


router.beforeEach((to, from, next) => {
  var userInfo = JSON.parse(localStorage.getItem("userInfo"))
  if (!userInfo && to.path != "/home") {
    next("/")
  } else {
    next()
  }
})
export default router
